body {
  font-family: -apple-system,system-ui,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #292b2c;
  background-color: #fafafa;
}

.container {
  padding: 15px;
}

.commit-file {
  background-color: #ffffff;
  -webkit-box-shadow: 0 0 5px lightgray;
          box-shadow: 0 0 5px lightgray;
  margin-top: 5ch;
}

.file-name {
  background-color: #f5f5f5;
  padding: 1ch;
}

.file-name span {
  opacity: 0.54;
}

.file-lines {
  font-family: 'Inconsolata';
  font-size: 14px;
  padding: 2ch;
  line-height: 2.75ex;
  display: -ms-flexbox;
  display: flex;
  width: 225ch;
  overflow: hidden;
}

.file-lines .gutter {
  width: 7ch;
  padding: 0 1ch;
  text-align: right;
  white-space: pre;
  opacity: 0.09;
  z-index: 10;
}

.file-lines .lines {
  white-space: pre;
  width: 100ch;
  color: rgb(41, 43, 44);
}

.file-lines .alignment {
  width: 5ch;
}

.gutter-left, .lines-left {
  position: relative;
}

.chunk-meta {
  background-color: #e3f2fd;
  opacity: 0.5;
}

.chunk-- {
  background-color: #ffebee;
  margin-left: -9ch;
  padding-left: 9ch;
}

.chunk-\+ {
  background-color: #e0f2f1;
  margin-left: -9ch;
  padding-left: 9ch;
}

.chunk-blank {
  border: 1px solid black;
  margin-top: -1px;
  margin-bottom: -1px;
  opacity: 0.09;
}

.horizon {
  width: 100%;
  height: 1px;
  background-color: #E0E0E0;
  position: fixed;
  top: 50%;
  z-index: 1;
}


